@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap');

body {}

.app {
  height: 100vh;
  padding-top: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.637)),
    url("https://i.imgur.com/JLMfo17.jpg");
  background-size: cover;
  background-position: center;
  background-position-y: 30px;
}

.selectArea {
  width: 50vw;
  height: 20vh;
  background-color: transparent;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.selectArea button {
  margin: 30px 0;
  height: 50px;
  width: 100px;
  border-radius: 20px;
  background-color: rgb(9, 12, 182);
  color: rgb(219, 231, 248);
  font-size: 1.3em;
  font-weight: 800;
  text-decoration: none;
}

.selectArea button:focus {
  outline: 0;
}

select {
  padding: 5px;
  border: solid 1px white;
  font: 1.5em Montserrat, sans-serif;
  color: white;
  background-color: transparent;
}

option {
  color: white;
  background-color: black;
}

.displayArea {
  margin-top: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30vh;
  width: 80vw;
  border: solid 2px white;
}

.displayArea p {
  text-align: center;
  color: white;
  font-size: 1.4rem;
  font-weight: 600;
  padding: 0 10px;
}